.dropmenu {
  position: relative;
  font-size: 14px;
}
.dropmenu .trigger {
  display: block;
  width: 100%;
  height: 36px;
  border: 1px solid #e0e0e0;
  padding: 0 28px 0 16px;
  text-align: left;
  transition: all 0.1s;
  position: relative;
}
.dropmenu .trigger:after {
  content: "";
  display: block;
  position: absolute;
  right: 17px;
  top: 50%;
  background: url("../img/common/select-icon.png") no-repeat 0 0;
  width: 12px;
  height: 6px;
  transform: translateY(-50%) rotate(0deg);
  transition: all 0.1s;
}
.dropmenu .trigger:hover {
  background-color: #f8f8f8;
  border-color: #999999;
}
.dropmenu .trigger.active {
  background-color: #f8f8f8;
  border-color: #999999;
}
.dropmenu .trigger.active:after {
  transform: translateY(-50%) rotate(180deg);
}
.dropmenu .trigger.active + div {
  display: block;
}
.dropmenu > div {
  display: none;
  position: absolute;
