  background-color: #264998;
  color: #fff;
}
.faq__li.active .faq__subject:before {
  transform: rotate(45deg) translateY(-50%);
}
.faq__subject {
  position: relative;
  display: flex;
  padding: 18px;
  padding-left: 60px;
  align-items: center;
  border-bottom: 1px solid #e1e1e1;
  cursor: pointer;
  transition: all 0.15s;
}
.faq__subject:after {
  content: "Q";
  display: block;
  position: absolute;
  left: 25px;
  top: 10px;
  border-radius: 50%;
  text-align: center;
  line-height: 28px;
  height: 28px;
  width: 28px;
  background-color: #f1f1f1;
  color: #333333;
  font-weight: 500;
  transition: all 0.15s;
}
.faq__subject:before {
  content: "";
  display: block;
  transition: all 0.15s;
  position: absolute;
  right: 25px;
  top: 50%;
  transform: rotate(0deg) translateY(-50%);
  width: 16px;
  height: 16px;
  background: url("../img/common/faq-off.png") no-repeat 50% 50%;
}
