.modal {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}
.modal.active {
  opacity: 1;
  visibility: visible;
}
.modal__container {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0 8px;
}
.modal__container:after {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.modal__body {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  text-align: left;
  z-index: 1045;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  background-color: #fff;
  border-radius: 4px;
  padding: 50px;
}
.modal__content {
  padding-top: 38px;
  color: #666666;
  font-size: 14px;
  line-height: 1.45;
}
.modal__close {
  position: absolute;
  right: -50px;
  top: 8px;
  width: 35px;
  height: 34px;
  background: url("../img/common/modal-close.png") no-repeat 0 0;
}
.modal__header h2 {
  font-size: 18px;
}
