.paging {
  text-align: center;
  font-size: 0;
}
.paging span {
  display: inline-block;
  vertical-align: top;
}
.paging a {
  vertical-align: top;
  display: inline-block;
  text-align: center;
  width: 28px;
  height: 28px;
  line-height: 28px;
  transition: all 0.1s;
  border-radius: 3px;
}
.paging .fir,
.paging .end,
.paging .prev,
.paging .next {
  font-size: 0;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.paging .fir.disabled,
.paging .end.disabled,
.paging .prev.disabled,
.paging .next.disabled {
  color: #dddddd;
  cursor: default;
}
.paging .prev {
  background-image: url("../img/common/paging-prev.png");
}
.paging .prev:hover {
  background-image: url("../img/common/paging-prev-hover.png");
}
.paging .fir {
  background-image: url("../img/common/paging-fir.png");
}
.paging .fir:hover {
  background-image: url("../img/common/paging-fir-hover.png");
}
.paging .next {
  background-image: url("../img/common/paging-next.png");
}
.paging .next:hover {
  background-image: url("../img/common/paging-next-hover.png");
}
.paging .end {
  background-image: url("../img/common/paging-end.png");
}
.paging .end:hover {
  background-image: url("../img/common/paging-end-hover.png");
}
.paging .page {
  font-size: 13px;
}
.paging .page.current, .paging .page:hover, .paging .page:focus {
  color: #333333;
  background-color: #e1e1e1;
  font-weight: 700;
}

